import axios from 'axios';
import _ from 'lodash';
const user = JSON.parse(localStorage.getItem('user-infos'));
const defaults = {
  baseURL: process.env.API_URL || 'http://localhost:1200/api/',

  headers: () => {
    return {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: user && user.token ? user.token : {},
    };
  },
  error: {
    code: 'INTERNAL_ERROR',
    message:
      "Quelque chose s'est mal pass. Veuillez vrifier votre connexion Internet ou contacter notre support.",
    status: 503,
    data: {},
  },
};
const request = axios;
request.interceptors.request.use(
  config => {
    if (!config.headers.Authorization) {
      const user = JSON.parse(localStorage.getItem('user-infos'));
      if (user?.token)
        request.defaults.headers.common.Authorization = `${user?.token}`;
    }
    return config;
  },
  error => console.log(error)
);
request.interceptors.response.use(
  response => {
    if (_.isUndefined(response)) {
      return [];
    } else {
      return response;
    }
  },
  error => console.log(error)
);

//console.log( user.token);
const api = (method, url, hasFile, variables) =>
  new Promise((resolve, reject) => {
    let formData = variables;
    request({
      url: `${url}`,
      method,
      headers: defaults.headers(),
      params: method == 'get' ? variables : undefined,
      data: method !== 'get' ? formData : undefined,
    }).then(
      response => {
        if (_.isUndefined(response)) {
          resolve([]);
        } else {
          resolve(response.data);
        }
      },
      error => {
        if (error?.response) {
          if (error?.response?.data?.error) {
            reject(error?.response?.data?.error);
          } else if (error?.response.data?.errors) {
            reject(error?.response.data?.errors);
          }
        } else {
          reject(defaults.error);
        }
      }
    );
  });
// eslint-disable-next-line import/no-anonymous-default-export
export default {
  get: (...args) => api('get', ...args),
  post: (...args) => api('post', ...args),
  put: (...args) => api('put', ...args),
  patch: (...args) => api('patch', ...args),
  delete: (...args) => api('delete', ...args),
};
